.card {
    background-color: #fff;
    border-radius: 20px;
    padding: 16px 24px;
    margin: 16px 16px;
    box-shadow: rgba(149, 157, 165, 0.2) 0 8px 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.text-container {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 0;
    justify-content: center;
    align-items: flex-start;
}

.current-sleep {
    color: #A9AAB0;
    padding: 0;
    margin: 4px 0;
}

.future-sleep {
    color: rgb(115,115,115);
    padding: 0;
    font-weight: bold;
    margin: 4px 0;
}

.time-stamp {
    font-size: 14px;
    color: #A9AAB0;
    font-weight: bold;
    padding-bottom: 0;
    margin-bottom: 0;
}

.accent {
    color: #77BFA3;
}


img {
    height: 80px;
    filter: invert(45%);
}

.card-button {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 30px;
    height: 30px;
    border-radius: 100px;
    margin: 0;
    padding: 0;
}

.invert {
    transform: scaleX(-1);
}