.container {
    display: flex;
    padding: 0;
    margin: 0;
    height: 100vh;
    background-color: #F0F8FFFF;
    flex-direction: column;
    justify-content: space-around;
}

.test {
    pointer-events: none;
    height: 75%;
    width: 100%;
    position: fixed;
    z-index: 1;
    background: linear-gradient(180deg, rgba(240, 248, 255, 0) 95%, #F0F8FFFF 100%);
}

.card-container {
    height: 75%;
    position: relative;
    overflow: scroll;
}

h3 {
    color: #484D64;
    margin: 0;
    padding: 0 20px 12px;
    font-size: 18px;
}

.button-container {
    display: flex;
    flex-direction: row;
    padding: 0 5px;
    justify-content: center;
}

button {
    align-items: center;
    margin: 10px;
    width: 50%;
    justify-content: center;
    padding: 16px 20px;
    border: none;
    border-radius: 20px;
    box-shadow: rgb(253,103,70, .5) 0px 6px 12px;
    color: #fff;
    font-weight: bold;
    font-size: 16px;
    background-color: #fd6746
}

button:disabled {
    box-shadow: rgb(115,115,115, .5) 0px 6px 12px;
    color: white;
    background-color: rgb(115,115,115);
}
